@import url('https://fonts.googleapis.com/css?family=Roboto');
@import url('https://fonts.googleapis.com/css?family=Montserrat');
@import url('https://fonts.googleapis.com/css?family=Merriweather');
@import url('https://fonts.googleapis.com/css?family=Open+Sans');
@import url('https://fonts.googleapis.com/css?family=Raleway');
@import url('https://fonts.googleapis.com/css?family=Lato');
@import url('https://fonts.googleapis.com/css?family=Noto+Serif');

.what-card {

}

.merriweather-type {
  /*font-family: 'Merriweather', sans-serif;*/
}

.outer-container {
  padding: 0px 80px;
}

.what-card-header {
  /*background-color: #f1f1f1;*/

  display: -ms-flexbox;

  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;

  padding: 20px 80px;

  /*text-transform: uppercase;*/
}

.what-card-header div {
  font-size: 18pt;
  font-weight: bold;
  text-transform: uppercase;
  color: #d1d1d1;
  /*color: #b70944;*/
}

.what-card-nav {
  margin-left: 20px;
}

.flex-spacer {
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.what-card-header .what-card-nav {

}

.what-card-header div:hover {
  color: #dc2966;
}

.row-card-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.what-card-info {
  background: #0dafe4;
  margin-right: 20px;

}

.what-card-action {
  background: #dc2966;

}

.card-type {
  padding: 30px;
  border-radius: 8px;
  color: white;
  -webkit-box-shadow: 1px 1px 8px #e1e1e1;
          box-shadow: 1px 1px 8px #e1e1e1;
  height: 350px;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.what-card-all {
  background: #f9f9f9;
  color: #dc2966;
  margin-top: 20px;
  height: 500px;
  -webkit-box-shadow: 0px 0px 8px #e9e9e9;
          box-shadow: 0px 0px 8px #e9e9e9;
  /*padding: 30px 0px;*/
  /*text-shadow: 1px 1px 1px #e1e1e1;*/
  margin-bottom: 80px;
}

.what-card-all h1 {
  border-bottom: 8px solid;
  padding: 0px 0px 15px 0px;
  width: 100%;
}

.what-card-title {
  font-size: 1.5em;
}

.what-card-intro {
  font-size: large;
}

.card-button {
  text-align: center;
  padding: 20px 20px;
  background: white;
  color: #dc2966;
  border: none;
  text-transform: uppercase;
  font-size: 20px;
  font-weight: bold;
  /*width: 150px;*/
  /*border-radius: 75px;*/
  /*border: 3px solid;*/
}

.inverse-button {
  color: white;
  background: #dc2966;
}

.card-button:hover {
  outline: 3px solid white;
  outline-offset: -3px;
  background: #dc2966;
  color: white;
}

.inverse-button:hover {
  outline: 3px solid white;
  outline-offset: -3px;
  color: #dc2966;
  background: white;
}



